// HXUI

const DEFAULT = 'default'
const WARNING = 'warn'
const ERROR = 'error'
const SUCCESS = 'success'
const FATAL = 'fatal'

export const Levels = {
  DEFAULT,
  WARNING,
  ERROR,
  SUCCESS,
  FATAL
}

export const levelArray = [
  { key: DEFAULT, color: 'cornflowerblue', text: '' },
  { key: SUCCESS, color: 'rgb(135, 208, 104)', text: '' },
  { key: WARNING, color: '#ff8f02', text: '' },
  { key: ERROR, color: '#ff5a50', text: '' },
  { key: FATAL, color: '#000000', text: '' }
]
// 
export const levelFilter = (text, target = 'text') => {
  let result = levelArray.filter(v => v.key === text)
  console.log('WARN', result)
  if (result.length) {
    return result[0][target]
  }
  return levelArray[0][target] //  default 
}

export const Positions = {
  TOP: 'top',
  CENTER: 'center',
  BOTTOM: 'bottom',
  TOP_LEFT: 'topLeft',
  TOP_RIGHT: 'topRight',
  BOTTOM_LEFT: 'bottomLeft',
  BOTTOM_RIGHT: 'bottomRight'
}
